import React from "react"
import ReactDOM from "react-dom"
import "./assets/index.css"
import App from "./App"

import { BrowserRouter } from "react-router-dom"

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter basename="/kwitter-zaska-afk">
      <App />
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById("root")
)
